import { apiGet, apiPost } from "./api";

export function getyzm() {
    return new Promise((resolve) => {
        apiGet('getcode').then(res => {
            resolve(res)
      })
    })
}

export function denlu(params) {
     return new Promise((resolve) => {
        apiPost('login',params).then(res => {
            resolve(res)
      })
    })
}
export function zhuce(params) {
     return new Promise((resolve) => {
        apiPost('register',params).then(res => {
            resolve(res)
      })
    })
}